import { useEffect, useRef, useState } from 'react';

// For the sake of this standalone Next.js app, we implement a simple REST-based 
// signaling bridge since we can't easily start a separate Socket.io server 
// in this environment. In production, this would use the real server.js.

type SignalMessage = {
  type: 'offer' | 'answer' | 'candidate';
  payload: any;
  from: string;
};

export const useSignaling = (roomId: string | null) => {
  const [messages, setMessages] = useState<SignalMessage[]>([]);
  const lastFetchedRef = useRef<number>(Date.now());

  // In a real scenario, this would use Socket.io or WebSockets.
  // This is a simulated real-time signaling via polling for the demo.
  useEffect(() => {
    if (!roomId) return;

    const interval = setInterval(async () => {
      // Simulation: Check a global or local store for messages
      // This part would normally be socket.on('message')
    }, 1000);

    return () => clearInterval(interval);
  }, [roomId]);

  const sendSignal = async (to: string, message: SignalMessage) => {
    console.log('Sending signal to', to, message);
    // Real implementation: socket.emit('signal', { to, message })
  };

  return { sendSignal, messages };
};

export const generateId = () => {
  return Math.floor(100000 + Math.random() * 900000).toString();
};
